.search{
  display: flex;
  justify-content: flex-end;
  margin-right: 1rem;
}
.search input{
  border: none;
  padding: 0.5rem;
  background-color: whitesmoke;
  color: gray;
  border-radius: 10px;
}

.search button{
  border-radius: 10px;
  background-color: #00fc87;
  border: none;
  padding: 0.5rem;
  color: #000000;
  margin-left: 2vh;
  cursor: pointer;
  font-weight: bolder;
}
.search button a{
  color: #000000;
}

